<template>
    <!-- 新增船舶待运 -->
    <div class="transport">
        <mu-appbar class="appbar">
            <mu-button slot="left" color="#fff" @click="$router.back()" icon>
                <mu-icon value="navigate_before"/>
            </mu-button>
            <div class="title">船舶待运</div>
            <mu-button slot="right" color="#fff" @click="handlerSubmit" flat>
                发送
            </mu-button>
        </mu-appbar>
        <div class="scroll-wrapper">
            <mu-form class="form" :model="form" label-position="left" label-width="100" ref="form">
                <mu-form-item class="form-item-s border-1px-b" prop="name" label="船舶名称" :rules="nameRules">
                    <mu-text-field v-model="form.name" placeholder="请输入船舶名称" prop="name" solo/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="type" label="船舶类型" :rules="shipTypeRules">
                    <mu-select v-model="form.type" placeholder="请输入船舶类型" solo>
                        <mu-option v-for="(option, index) in shipTypeOptions" :key="option.name" :label="option.name"
                                   :value="option.name"/>
                    </mu-select>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="weight" label="载重吨" :rules="weightRules">
                    <mu-text-field v-model="form.weight" placeholder="请输入载重吨"
                                   suffix="吨" prop="weight" type="number"
                                   solo/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="length" label="船长" :rules="lengthRules">
                    <mu-text-field v-model="form.length" placeholder="请输入船长"
                                   suffix="米" prop="length" type="number"
                                   solo/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="width" label="船宽" :rules="widthRules">
                    <mu-text-field v-model="form.width" placeholder="请输入船宽"
                                   suffix="米" prop="width" type="number"
                                   solo/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="transportPort" label="待运港" :rules="portRules">
                    <mu-select v-model="form.transportPort" placeholder="待运港" solo>
                        <mu-option v-for="(option, index) in portTypeOptions" :key="option.name" :label="option.name"
                                   :value="option.name"/>
                    </mu-select>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" label="待运时间" prop="time"
                              :rules="timeRules">
                    <mu-date-input v-model="form.time"
                                   type="dateTime"
                                   view-type="list"
                                   container="bottomSheet"
                                   placeholder="请选择装货时间"
                                   solo/>
                </mu-form-item>
                <div>
                    <mu-sub-header>备注</mu-sub-header>
                    <mu-text-field v-model="form.detail" class="border-1px-b" style="padding: 0 16px 30px"
                                   placeholder="请描述更多信息"
                                   :rows="4"
                                   :max-length="200"
                                   full-width
                                   multi-line
                                   solo/>
                </div>
            </mu-form>
        </div>
    </div>
    <!-- 新增船舶待运 -->
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import {saveMoment} from "../../api/dynamic"
    import {queryTypeDictByFid} from "../../api/search"
    import {isArray} from "../../types/index"
    import FormatTime from '../../common/utils/time'

    export default {
        name: "transport",
        data() {
            return {
                form: {
                    name: "",
                    type: "",
                    weight: "",
                    length: "",
                    width: "",
                    transportPort: "",
                    time: "",
                    detail: ""
                },
                /***
                 * 表单验证
                 */
                nameRules: [
                    {validate: val => !!val, message: '必须填写船舶名称'},
                    {validate: val => val.length >= 2, message: '船舶名称长度大于或等于 2 '}
                ],
                weightRules: [
                    {validate: val => !!val, message: '必须填写船舶重量'},
                    {validate: val => /^(-?\d+)(\.\d{1,2})?$/.test(val), message: '请精确到小数点后两位'}
                ],
                lengthRules: [
                    {validate: val => !!val, message: '必须填写船舶长度'},
                    {validate: val => /^(-?\d+)(\.\d{1,2})?$/.test(val), message: '请精确到小数点后两位'}
                ],
                widthRules: [
                    {validate: val => !!val, message: '必须填写船舶宽度'},
                    {validate: val => /^(-?\d+)(\.\d{1,2})?$/.test(val), message: '请精确到小数点后两位'}
                ],
                timeRules: [
                    {validate: val => !!val, message: '必须填写装货时间'},
                ],
                shipTypeRules: [
                    {validate: val => !!val, message: '必须选择船舶类型'},
                    {
                        validate: val => this.shipTypeOptions.findIndex(item => item.name === val) !== -1,
                        message: '请选择正确的船舶类型'
                    }
                ],
                portRules: [
                    {validate: val => !!val, message: '必须选择港口'},
                    {
                        validate: val => this.portTypeOptions.findIndex(item => item.name === val) !== -1,
                        message: '请选择正确的港口类型'
                    }
                ],
                phoneRules: [
                    {validate: val => !!val, message: '必须填写手机号码'},
                    {validate: val => /^1\d{10}$/.test(val), message: '请输入正确的手机号码'}
                ],
                /***
                 * 下拉选项：
                 *      value:  值
                 *      name:  名称
                 */
                shipTypeOptions: [],
                portTypeOptions: []
            }
        },
        created() {
            this.queryTypeDictByFid()
        },
        activated() {
            this.form = {
                name: "",
                type: "",
                weight: "",
                length: "",
                width: "",
                transportPort: "",
                time: "",
                detail: ""
            }
        },
        methods: {
            async queryTypeDictByFid() {
                this.shipTypeOptions = isArray(await queryTypeDictByFid('010'))
                this.portTypeOptions = isArray(await queryTypeDictByFid('019'))
            },
            handlerSubmit() {
                this.$refs.form.validate().then(result => {
                    if (result) {
                        const loading = this.$loading();

                        const data = {
                            content: this.splicingContent(),
                            type: '004003',
                            mediaType: '003002',
                            status: '020002'
                        }

                        saveMoment(data)

                        loading.close()

                        this.$toast('发表成功！')

                        this.$emit('refresh')

                        this.$router.go(-2)
                    }
                })
            },
            splicingContent() {
                return "船舶名称:" + this.form.name + "<br />" +
                    "船舶类型:" + this.form.type + "<br />" +
                    "载重吨:" + this.form.weight + "吨<br />" +
                    "船长:" + this.form.length + "米<br />" +
                    "船宽:" + this.form.width + "米<br />" +
                    "待运港:" + this.form.transportPort + "<br />" +
                    "待运时间:" + this.form.time + "<br />" +
                    "备注:" + this.form.detail + "<br />"
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    .transport
        padding-top 46px
        background-color #fff
        .appbar
            position absolute
            top 0
            left 0
            z-index 1
            width 100%
            height 46px
            box-shadow none
            background-color #3899D7
            .title
                color #fff
                line-height 46px
                text-align center
                font-size 15px
        .scroll-wrapper
            width 100%
            height 100%
            .form
                .form-item-s
                    margin 0
                    padding 10px
                    .mu-form-item-help
                        bottom 5px
                .form-item-buttons
                    padding 20px 0
                    .button
                        color #fff
                        background-color #2196f3
                        &.clear
                            color #999
                            background-color #fff
                .button-wrapper
                    padding 20px 0

</style>



